.todo-list-item {
    font-size: 1.25rem;
    & button {
      width: 35px;
      margin: 3px;
    }
    &-label {
      margin-left: 1.25rem;
      line-height: 35px;
      cursor: pointer;
      user-select: none;
    }
    &.done .todo-list-item-label {
      text-decoration: line-through;
    }
    &.important .todo-list-item-label {
      font-weight: bold;
      color: #d1332e;
    }
  }
  
.active {
  background-color: danger;
}
